}
.card {

  width: 450px;
  height:150px;
  margin:10px;
  border: 1px solid #354189;
  border-radius: 8px;
  background: #292d57;
  box-sizing: border-box;
  display:grid;
  text-align: center;
  margin-top: 30px;
  
}
.lod{
  margin:auto;
  margin-top: 250px;
  
}
@media only screen and (min-device-width: 400px) and (max-device-width: 650px) {
  .card{
    width:350px;
    
      
    
  }
  .slk{
    margin-top: 50px;
    width: 270px;
    justify-self: center;
  }
  .textp{
    width: 300px;
    color:#a09edd;
    margin-bottom: 100px;
  }
  .lod{margin-top: 200px;
  size:15;}
}
@media only screen and (min-device-width: 320px) and (max-device-width: 568px) {
  .card{
    width:320px;
  }
  .lod{
    margin-top: 200px;
    size:15;
  }
  .slk{
    margin-top: 20px;
    width: 250px;
    justify-self: center;
  }
  .textp{
    width: 300px;

  }
}
.bioDiv{
  display:grid;
  justify-content:center;
  text-align: center;
  
  
}
.container {
  
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.wrapper{
  
  display:flex;
  flex-wrap: wrap;  
  gap:5%;
  justify-content: center;

}
.elem{
  padding: 5px;
  margin-top: 10px;
}
.center {
  
  display: flex;
  align-items: center;
  justify-content: center;
}
