.volume-slider {
  width:110px;
  height: 80%;
}
button {
  font-size: 14px;
  color: #fff;
  background-color: #333;
  border: none;
  padding: 10px 20px;
  cursor: pointer;}
  button:hover {
    background-color: #555;
  }
  button:active {
    background-color: #777;
  }
input[type="range"] {
    /* Change the appearance of the sliders */
    -webkit-appearance: none;
    height: 15px;
    background-color:#a09edd;
    outline: none;
    border-radius: 20px;
    margin-left:7px;
    margin-right:7px;
    padding-left: 2px;
    padding-right: 2px;
  }

    input::-webkit-slider-thumb {
      
      -webkit-appearance: none;
      width: 12px;
      height: 12px;
      background-color: #131533;
      border-radius: 50%;
      cursor: pointer
      ;
    }
    input::-moz-range-thumb {
      width: 12px;
      height: 12px;
      background-color: #131533;
      border-radius: 50%;
      cursor: pointer;
    }

